<template>
    <div>
      <div class="block-wrap">
        <div class="block-sub-title">成本产出模型管理</div>
        <div class="item-wrap">
          <ul class="cost-item">
            <li><label>设备ID：</label><span>20376583</span></li>
            <li><label>单元类型：</label><span>光伏电站</span></li>
            <li><label>容量：</label><span>50MW</span></li>
            <li><label>效率：</label><span>76%</span></li>
            <li>
              <label>运行模式</label>
              <el-select v-model="value" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </li>
          </ul>
        </div>
        <div id="costModel"></div>
      </div>
    </div>
</template>

<script>
  import echarts from "echarts";
    export default {
        data () {
            return {
              options: [{
                value: '选项1',
                label: '黄金糕'
              }, {
                value: '选项2',
                label: '双皮奶'
              }, {
                value: '选项3',
                label: '蚵仔煎'
              }, {
                value: '选项4',
                label: '龙须面'
              }, {
                value: '选项5',
                label: '北京烤鸭'
              }],
              powerOptions: [{
                value: '100',
                label: '100kW'
              }],
              timeOptions: [{
                value: '6',
                label: '6小时'
              }],
              runningOptions: [{
                value: '6',
                label: '离网运行'
              }],
              value: '',
              powerValue:'',
              timeValue:'',
              runningValue:''
            }
        },
      mounted() {
        let that = this;
        that.getCostModel();

      },
      methods:{
        getCostModel(){
          var that = this;
          // 基于准备好的dom，初始化echarts实例
          var myChart = echarts.init(document.getElementById('costModel'));
          // 指定图表的配置项和数据
          var option = {
            color: ['#0A6EDA'],
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            xAxis: {
              type: 'category',
              name:'W(kWh)',
              //X轴数字
              axisLabel:{
                color:'#2B2B2B'
              },
              //X轴刻度线
              axisTick:{
                show:false
              },
              //纵向坐标网格线
              splitLine:{
                show:true,
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
              //X轴坐标线
              axisLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
              boundaryGap: false,
              data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
            },
            yAxis: {
              type: 'value',
              name:'元(成本)',
              //Y轴数字
              axisLabel:{
                color:'#2B2B2B'
              },
              //Y轴刻度线
              axisTick:{
                show:false
              },
              //横向坐标网格线
              splitLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
              //Y轴坐标线
              axisLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
            },
            grid: {
              x: '2%',
              y: '10%',
              x2: '5%',
              y2: '24%'
            },
            series: [{
              data: [820, 932, 901, 934, 1290, 1330, 1320],
              type: 'line'
            }]
          };
          // 使用刚指定的配置项和数据显示图表。
          myChart.setOption(option);
        },
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



