<template>
  <div>
    <div class="block-wrap">
      <div class="block-sub-title">三联供设备参数</div>
      <div class="item-wrap">
        <ul class="cost-item">
          <li><label>设备ID：</label><span>{{devId}}</span></li>
          <li><label>单元类型：</label><span>{{unitType}}</span></li>
          <li><label>容量：</label><span>{{ratedPower}}kWh</span></li>
          <li><label>发电效率：</label><span>{{generationEfficiency}}%</span></li>
          <li><label>WTFcw：</label><span>{{WTFcw}}%</span></li>
          <li><label>综合效率：</label><span>{{allEfficiency}}%</span></li>
          <li><label>燃气价格：</label><span>{{gasPrice}}元/nm³</span></li>
          <li><label>燃气低位发热量：</label><span>{{gasLowHot}}MJ/m³</span></li>
          <li><label>COPer：</label><span>{{COPer}}%</span></li>
          <li><label>COPab：</label><span>{{COPab}}</span></li>
        </ul>
      </div>
      <div class="block-sub-title">三联供设定参数</div>
      <div class="item-wrap">
        <ul class="cost-item">
          <li>
            <label>供热总量</label>
            <el-select v-model="heatValue" placeholder="请选择">
              <el-option
                v-for="item in heatOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </li>
          <li>
            <label>供冷总量</label>
            <el-select v-model="coldValue" placeholder="请选择">
              <el-option
                v-for="item in coldOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </li>
          <li>
            <label>年发电量</label>
            <el-select v-model="yearPowerValue" placeholder="请选择">
              <el-option
                v-for="item in yearPowerOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </li>
          <li>
            <label>运行方式</label>
            <el-select v-model="runningValue" placeholder="请选择">
              <el-option
                v-for="item in runningOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </li>
        </ul>
      </div>
      <div id="CCHPChart"></div>
    </div>
  </div>
</template>

<script>
  import echarts from "echarts";
  export default {
    data () {
      return {
        heatOptions: [{
          value: '400',
          label: '400MJ'
        }],
        coldOptions: [{
          value: '6',
          label: '0.3元/kWh'
        }],
        yearPowerOptions: [{
          value: '6',
          label: '500万/kWh'
        }],
        runningOptions: [{
          value: '6',
          label: '离网运行'
        }],
        value: '',
        heatValue:'',
        coldValue:'',
        yearPowerValue:'',
        runningValue:'',
        devId: "",//设备ID
        unitType: "",//单元类型
        ratedPower:"",//容量
        generationEfficiency:"",//发电效率
        WTFcw:"",//WTFcw
        allEfficiency:"",//综合效率
        gasPrice:"",//燃气价格
        gasLowHot:"",//燃气低位发热量
        COPer:"",//COPer
        COPab:"",//COPab
      }
    },
    mounted() {
      let that = this;
      that.getCCHPChart();
      that.getCosts();
    },
    methods:{
      getCosts(){
        let that = this;
        that.$http
          .get(this.BASE_INFO.BASE_URL+"api/cost/getCCHPCosts", {})
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              that.getCCHPData(data);
            }
          });
      },
      getCCHPData(data){
        if(data){
          let that = this;
          let xData = [];
          let seriesData = [];
          for(var item of data){
            that.devId = item.devId;//设备ID
            that.unitType = item.unitType;//单元类型
            that.ratedPower = item.ratedPower;//容量
            that.generationEfficiency = item.generationEfficiency;//发电效率
            that.WTFcw = item.WTFcw;//WTFcw
            that.allEfficiency = item.allEfficiency;//综合效率
            that.gasPrice = item.gasPrice;//燃气价格
            that.gasLowHot = item.gasLowHot;//燃气低位发热量
            that.COPer = item.COPer;//COPer
            that.COPab = item.COPab;
            for(var xda of item.data){
              seriesData.push(xda.value);
              xData.push(xda.time);
            }
          }
          that.getCCHPChart(xData,seriesData);
        }
      },
      getCCHPChart(xData,seriesData){
        var that = this;
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById('CCHPChart'));
        // 指定图表的配置项和数据
        var option = {
          color: ['#0A6EDA'],
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              crossStyle: {
                color: '#999'
              }
            }
          },
          xAxis: {
            type: 'category',
            name:'W(kWh)',
            //X轴数字
            axisLabel:{
              color:'#2B2B2B'
            },
            //X轴刻度线
            axisTick:{
              show:false
            },
            //纵向坐标网格线
            splitLine:{
              show:true,
              lineStyle:{
                color:'#e6e8ed'
              }
            },
            //X轴坐标线
            axisLine:{
              lineStyle:{
                color:'#e6e8ed'
              }
            },
            boundaryGap: false,
            data: xData
          },
          yAxis: {
            type: 'value',
            name:'元(成本)',
            //Y轴数字
            axisLabel:{
              color:'#2B2B2B'
            },
            //Y轴刻度线
            axisTick:{
              show:false
            },
            //横向坐标网格线
            splitLine:{
              lineStyle:{
                color:'#e6e8ed'
              }
            },
            //Y轴坐标线
            axisLine:{
              lineStyle:{
                color:'#e6e8ed'
              }
            },
          },
          grid: {
            x: '4%',
            y: '10%',
            x2: '5%',
            y2: '24%'
          },
          series: [{
            data: seriesData,
            type: 'line'
          }]
        };
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



