<template>
    <div>
      <div class="block-wrap">
        <div class="block-sub-title">储能设备参数</div>
        <div class="item-wrap">
          <ul class="cost-item">
            <li><label>设备ID：</label><span>{{devId}}</span></li>
            <li><label>单元类型：</label><span>{{unitType}}</span></li>
            <li><label>容量：</label><span>{{ratedPower}}kWh</span></li>
            <li><label>充电效率：</label><span>{{chargeEfficiency}}%</span></li>
            <li><label>放电效率：</label><span>{{generationEfficiency}}%</span></li>
            <li><label>循环次数：</label><span>{{cycleTime}}%</span></li>
            <li><label>固定投资成本：</label><span>{{investCost}}万元</span></li>
            <li><label>运维成本：</label><span>{{operationCost}}元/天</span></li>
          </ul>
        </div>
        <div class="block-sub-title">储能设定参数</div>
        <div class="item-wrap">
          <ul class="cost-item">
            <li>
              <label>充电时段</label>
              <el-select v-model="powerTimeValue" placeholder="请选择">
                <el-option
                  v-for="item in powerTimeOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </li>
            <li>
              <label>充电段电价</label>
              <el-select v-model="powerPriceValue" placeholder="请选择">
                <el-option
                  v-for="item in powerPriceOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </li>
            <li>
              <label>年利用率</label>
              <el-select v-model="yearURValue" placeholder="请选择">
                <el-option
                  v-for="item in yearUROptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </li>
          </ul>
        </div>
        <div id="SEChart"></div>
      </div>
    </div>
</template>

<script>
  import echarts from "echarts";
    export default {
        data () {
            return {
              powerTimeOptions: [{
                value: '400',
                label: '400MJ'
              }],
              powerPriceOptions: [{
                value: '6',
                label: '0.3元/kWh'
              }],
              yearUROptions: [{
                value: '6',
                label: '500次/年'
              }],
              value: '',
              powerTimeValue:'',
              powerPriceValue:'',
              yearURValue:'',
              devId: '',//设备ID
              unitType: "",//单元类型
              ratedPower:"",//容量
              chargeEfficiency:"",//充电效率
              generationEfficiency:"",//放电效率
              cycleTime:"",//循环次数
              investCost:"",//固定投资成本
              operationCost:"",//运维成本
            }
        },
      mounted() {
        let that = this;
        that.getSEChart();
        that.getCosts();
      },
      methods:{
        getCosts(){
          let that = this;
          that.$http
            .get(this.BASE_INFO.BASE_URL+"api/cost/getSECosts", {})
            .then(function(response) {
              if(response && response.data && response.data.data){
                var  data=response.data.data;
                that.getSEData(data);
              }
            });
        },
        getSEData(data){
          if(data){
            let that = this;
            let xData = [];
            let seriesData = [];
            for(var item of data){
              that.devId = item.devId;
              that.unitType = item.unitType;
              that.ratedPower = item.ratedPower;
              that.chargeEfficiency = item.chargeEfficiency;
              that.generationEfficiency = item.generationEfficiency;
              that.cycleTime = item.cycleTime;
              that.investCost = item.investCost;
              that.operationCost = item.operationCost;
              for(var xda of item.data){
                seriesData.push(xda.value);
                xData.push(xda.time);
              }
            }
            that.getSEChart(xData,seriesData);
          }
        },
        getSEChart(xData,seriesData){
          var that = this;
          // 基于准备好的dom，初始化echarts实例
          var myChart = echarts.init(document.getElementById('SEChart'));
          // 指定图表的配置项和数据
          var option = {
            color: ['#0A6EDA'],
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            xAxis: {
              type: 'category',
              name:'W(kWh)',
              //X轴数字
              axisLabel:{
                color:'#2B2B2B'
              },
              //X轴刻度线
              axisTick:{
                show:false
              },
              //纵向坐标网格线
              splitLine:{
                show:true,
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
              //X轴坐标线
              axisLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
              boundaryGap: false,
              data: xData
            },
            yAxis: {
              type: 'value',
              name:'元(成本)',
              //Y轴数字
              axisLabel:{
                color:'#2B2B2B'
              },
              //Y轴刻度线
              axisTick:{
                show:false
              },
              //横向坐标网格线
              splitLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
              //Y轴坐标线
              axisLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
            },
            grid: {
              x: '4%',
              y: '10%',
              x2: '5%',
              y2: '24%'
            },
            series: [{
              data: seriesData,
              type: 'line'
            }]
          };
          // 使用刚指定的配置项和数据显示图表。
          myChart.setOption(option);
        },
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



