<template>
    <div>
      <div class="block-wrap">
        <div class="block-sub-title">光伏设备参数</div>
        <div class="item-wrap">
          <ul class="cost-item">
            <li><label>设备ID：</label><span>{{devId}}</span></li>
            <li><label>单元类型：</label><span>{{unitType}}</span></li>
            <li><label>额定容量：</label><span>{{ratedPower}}kWh</span></li>
            <li><label>充电效率：</label><span>{{chargeEfficiency}}%</span></li>
            <li><label>光伏上网电价：</label><span>{{PVPrice}}元/kWh</span></li>
            <li><label>固定投资成本：</label><span>{{cost}}万元</span></li>
          </ul>
        </div>
        <div class="block-sub-title">光伏设定参数</div>
        <div class="item-wrap">
          <ul class="cost-item">
            <li>
              <label>当天预测平均功率</label>
              <el-select v-model="powerValue" placeholder="请选择">
                <el-option
                  v-for="item in powerOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </li>
            <li>
              <label>当天日照小时数</label>
              <el-select v-model="timeValue" placeholder="请选择">
                <el-option
                  v-for="item in timeOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </li>
            <li>
              <label>运行方式</label>
              <el-select v-model="runningValue" placeholder="请选择">
                <el-option
                  v-for="item in runningOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </li>
          </ul>
        </div>
        <div id="PVChart"></div>
      </div>
    </div>
</template>

<script>
  import echarts from "echarts";
    export default {
        data () {
            return {
              powerOptions: [{
                value: '100',
                label: '100kW'
              }],
              timeOptions: [{
                value: '6',
                label: '6小时'
              }],
              runningOptions: [{
                value: '6',
                label: '离网运行'
              }],
              value: '',
              powerValue:'',
              timeValue:'',
              runningValue:'',
              devId:'',//设备ID
              unitType:'',//单元类型
              ratedPower:'',//额定容量
              chargeEfficiency:'',//充电效率
              PVPrice:'',//光伏上网电价
              cost:'',//固定投资成本
            }
        },
      mounted() {
        let that = this;
        that.getPVChart();
        that.getCosts();
      },
      methods:{
        getCosts(){
          let that = this;
          that.$http
            .get(this.BASE_INFO.BASE_URL+"api/cost/getPVCosts", {})
            .then(function(response) {
              if(response && response.data && response.data.data){
                var  data=response.data.data;
                that.getPVData(data);
              }
            });
        },
        getPVData(data){
          if(data){
            let that = this;
            let xData = [];
            let seriesData = [];
            for(var item of data){
              that.devId = item.devId;
              that.unitType = item.unitType;
              that.ratedPower = item.ratedPower;
              that.chargeEfficiency = item.chargeEfficiency;
              that.PVPrice = item.PVPrice;
              that.cost = item.cost;
              for(var xda of item.data){
                seriesData.push(xda.value);
                xData.push(xda.time);
              }
            }
            that.getPVChart(xData,seriesData);
          }
        },
        getPVChart(xData,seriesData){
          var that = this;
          // 基于准备好的dom，初始化echarts实例
          var myChart = echarts.init(document.getElementById('PVChart'));
          // 指定图表的配置项和数据
          var option = {
            color: ['#0A6EDA'],
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            xAxis: {
              type: 'category',
              name:'W(kWh)',
              //X轴数字
              axisLabel:{
                color:'#2B2B2B'
              },
              //X轴刻度线
              axisTick:{
                show:false
              },
              //纵向坐标网格线
              splitLine:{
                show:true,
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
              //X轴坐标线
              axisLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
              boundaryGap: false,
              data: xData
            },
            yAxis: {
              type: 'value',
              name:'元(成本)',
              //Y轴数字
              axisLabel:{
                color:'#2B2B2B'
              },
              //Y轴刻度线
              axisTick:{
                show:false
              },
              //横向坐标网格线
              splitLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
              //Y轴坐标线
              axisLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
            },
            grid: {
              x: '4%',
              y: '10%',
              x2: '5%',
              y2: '24%'
            },
            series: [{
              data: seriesData,
              type: 'line'
            }]
          };
          // 使用刚指定的配置项和数据显示图表。
          myChart.setOption(option);
        },
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



