<template>
    <div>
      <TreeOrg :treeData="treeData" v-on:childByType="childByType"></TreeOrg>
      <div class="left chart-wrap">
        <el-row>
          <el-col :span="24">
            <div class="grid-content bg-purple-dark">
              <costModel v-if="cost"></costModel>
              <PVModel v-if="PV"></PVModel>
              <SEModel v-if="SE"></SEModel>
              <CCHPModel v-if="CCHP"></CCHPModel>
            </div>
          </el-col>
        </el-row>
        <!--<vue-highcharts :options="options" ref="lineCharts" style="margin-bottom: 5px;"></vue-highcharts>-->
        <!--<vue-highcharts :options="options1" ref="lineCharts1"></vue-highcharts>-->
      </div>
    </div>
</template>

<script>
  import echarts from "echarts";
  import costModel from "../../common/costModel.vue";
  import PVModel from "../../common/PVModel.vue";
  import SEModel from "../../common/SEModel.vue";
  import CCHPModel from "../../common/CCHPModel.vue";
  import TreeOrg from "../../common/TreeOrg.vue";
    export default {
        data () {
            return {
              cost:false,
              PV:false,
              CCHP:true,
              SE:false,
              treeData: [],
              options: [{
                value: '选项1',
                label: '黄金糕'
              }, {
                value: '选项2',
                label: '双皮奶'
              }, {
                value: '选项3',
                label: '蚵仔煎'
              }, {
                value: '选项4',
                label: '龙须面'
              }, {
                value: '选项5',
                label: '北京烤鸭'
              }],
              powerOptions: [{
                value: '100',
                label: '100kW'
              }],
              timeOptions: [{
                value: '6',
                label: '6小时'
              }],
              runningOptions: [{
                value: '6',
                label: '离网运行'
              }],
              value: '',
              powerValue:'',
              timeValue:'',
              runningValue:''
            }
        },
      mounted() {
        let that = this;
        that.getCostsTree();
        //that.getCostModel();
        //that.getPVChart();

      },
      methods:{
        childByType: function(childType) {
          console.log(childType.type);
          var that = this;
          if(childType.type == 'CCHP'){
            that.cost = false;
            that.PV = false;
            that.SE = false;
            that.CCHP = true;
          }else if(childType.type == 'PV'){
            that.cost = false;
            that.PV = true;
            that.SE = false;
            that.CCHP = false;
          }else if(childType.type == 'SE'){
            that.cost = false;
            that.PV = false;
            that.SE = true;
            that.CCHP = false;
          }
        },
        getCostsTree(){
          let that = this;
          that.$http
            .get(this.BASE_INFO.BASE_URL+"api/cost/getCostsTree", {})
            .then(function(response) {
              if(response && response.data && response.data.data){
                var  data=response.data.data;
                console.log(data);
                that.getTreeData(data);
              }
            });
        },
        getTreeData(data){
          if(data){
            let that = this;
            let xData = [];
            let seriesData = [];
            that.treeData = data;
          }
        },


      },
      components: {
        TreeOrg,costModel,PVModel,SEModel,CCHPModel
      },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



